body {
  font-family: Arial, sans-serif;
  background-color: #f0f0f0;
  margin: 0px;
  padding: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.title{
  margin-top: 150px;
  text-align: center;
  font-weight: bolder;
  font-size: 40px;
}

.first {
  overflow-x: hidden;
  display: grid;
  grid-template-columns: 33% 33% 33%;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
}

.second {
  width: 400px;
  box-shadow: 3px 2px 2px black;
  margin-bottom: 10px;
  text-align: center;
  border: 2px solid black;
  padding: 10px;
  margin-right: 10px;
  margin:20px;
  border-radius: 15px;
}
.count{
  margin-left: 50px;
}
.reset{
  height: 40px;
  width: 100px;
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  margin-left: 70px;
  cursor: pointer;
}
.second img {
  width: 250px;
  height: 150px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
  margin-left: 10px;
  transition: calc(0.4);
}
.second img:hover{
  transform: scale(1.1);
}
.remove{
  background-color: #e53935;
  color: #4CAF50;
}
.second h1 {
  font-size: 20px;
  margin: 10px 0;
  text-align: justify;
  margin-left: 70px;
}
.add{
  background-color: #4CAF50;
}
button{
  background-color: gray;
}
.second button {
  transition: calc(1.2);
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
}

.second button:hover{
  transform: scale(1.2);
}
.reset-button {
  background-color: #f44336;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s;
}

.reset-button:hover {
  background-color: #e53935;
}

h1 {
  font-size: 24px;
  color: #333;
}
